@font-face {
  font-family: 'mini-tutorial-icons';
  src: url('../font/mini-tutorial-icons.eot?7542464');
  src: url('../font/mini-tutorial-icons.eot?7542464#iefix') format('embedded-opentype'),
       url('../font/mini-tutorial-icons.woff2?7542464') format('woff2'),
       url('../font/mini-tutorial-icons.woff?7542464') format('woff'),
       url('../font/mini-tutorial-icons.ttf?7542464') format('truetype'),
       url('../font/mini-tutorial-icons.svg?7542464#mini-tutorial-icons') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
/*
@media screen and (-webkit-min-device-pixel-ratio:0) {
  @font-face {
    font-family: 'mini-tutorial-icons';
    src: url('../font/mini-tutorial-icons.svg?7542464#mini-tutorial-icons') format('svg');
  }
}
*/
[class^="__mt__icon-"]:before, [class*=" __mt__icon-"]:before {
  font-family: "mini-tutorial-icons";
  font-style: normal;
  font-weight: normal;
  speak: never;

  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: .2em;
  text-align: center;
  /* opacity: .8; */

  /* For safety - reset parent styles, that can break glyph codes*/
  font-variant: normal;
  text-transform: none;

  /* fix buttons height, for twitter bootstrap */
  line-height: 1em;

  /* Animation center compensation - margins should be symmetric */
  /* remove if not needed */
  margin-left: .2em;

  /* you can be more comfortable with increased icons size */
  /* font-size: 120%; */

  /* Font smoothing. That was taken from TWBS */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  /* Uncomment for 3D effect */
  /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
}

.__mt__icon-menu:before { content: '\f0c9'; } /* '' */
